import React from 'react';
import Events from '../Events';

export default class Account extends React.Component {

    RenderTable = (Title) => {

        switch(true) {
            default:
                return <div>
                    <h1>An error has occured.</h1>
                </div>
        }
    }

    render() {
        const {
            Oauth2,
            Cerner,
            AccountArray,
            AccountIndex,
            SetAppState
        } = this.props;

        return <div className="App-Account">
            <ul className="Account-Nav">
                {AccountArray.map((item, i) => {
                    if (AccountIndex === i) return <li key={i} 
                        className="Account-Nav-Link Account-Nav-Link-Active" 
                        onClick={() => Events.client.request(Oauth2, SetAppState, {
                            page: AccountArray[AccountIndex],
                            state: "AccountIndex",
                            value: i
                        })}
                    >{item}</li>
                    return <li key={i} 
                        className="Account-Nav-Link" 
                        onClick={() => Events.client.request(Oauth2, SetAppState, {
                            page: AccountArray[AccountIndex],
                            state: "AccountIndex",
                            value: i
                        })}
                    >{item}</li>
                })}
            </ul>
            {this.RenderTable(AccountArray[AccountIndex], Cerner)}
        </div>
    }
}